:root{
  font-size:10px;

  --myColor:#fff;
  --theme-bg-color:#fff;
  --btn-color:#ec4141;
  --sound-bar-height:40px;
  --progress-bar-width:0px;
  --login-width:320px;
  --login-height:450px;
}
/* @media (min-width:576px) {
  
